.mainPage-firstScreen {
  position: relative;
  display: flex;
  height: 892px;
  background: linear-gradient(
    90deg,
    #108c3d 49.87%,
    rgba(16, 140, 61, 0) 69.66%
  );

  .list {
    position: absolute;
    z-index: 10;
    bottom: 0;
  }

  &__col {
    display: flex;
    flex-direction: column;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 125px;
  }

  &__title {
    color: $white;
    font-family: "Angst";
    font-size: 66px;
    font-style: normal;
    font-weight: 50;
    line-height: normal;
  }

  &__title2 {
    color: $white;
    font-family: "AvenirNextCyr-Light";
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__button {
    z-index: 20;
    width: 458px;
    height: 70px;
    border: 3px solid $white;
    color: $white;
    text-align: center;
    font-family: "AvenirNextCyr-Bold";
    font-size: 35px;
    font-style: normal;
    font-weight: 750;
    line-height: normal;
    background: transparent;
    cursor: pointer;
    margin-top: 50px;
  }

  img {
    margin-left: auto;
    z-index: -1;
  }
}

